<script>
import axios from "axios";
import { mapMutations , mapState} from "vuex";


export default {
  name: "Home",
  components: {  },
  data: () => ({
    focus: "",
    type: "month",
    select: { modo: "Mes", tipo: "month" },
    items: [
      { modo: "Mes", tipo: "month" },
      { modo: "Semana", tipo: "week" },
      { modo: "Día", tipo: "day" },
    ],
    typeToLabel: {
      month: "Mes",
      week: "Semana",
      day: "Dia",
      "4day": "4 Days",
    },
    selectedEvent: {},
    selectedElement: null,
    selectedOpen: false,
    events: [],
    colors: [
      "blue",
      "indigo",
      "deep-purple",
      "cyan",
      "green",
      "orange",
      "grey darken-1",
    ],
    names: [
      "Enfermedad",
      "Maternidad",
      "Día Festivo",
      "Sistemas Operativos",
      "Cumpleaños",
      "Día Economico",
      "Conferencia",
      "Ingnieria De Sofware",
    ],
    fechas: [],
    //DATOS
      Info: "",
      Espera: true,
      Correcto: false,
      information:"",
      error:false
  }),
  watch: {
    type: function (value) {
      if (value == "month") {
        this.select = { modo: "Mes", tipo: "month" };
      } else if (value == "week") {
        this.select = { modo: "Semana", tipo: "week" };
      } else {
        this.select = { modo: "Día", tipo: "day" };
      }
    },
  },
  mounted() {    
    this.$refs.calendar.checkChange();    
  },
  created() {
    this.Checar_Token();
  },
  methods: {
    ...mapMutations(["Modificar_cargar","Checar_Token","Modificar_Informacion",
    "Modificar_Espera",
    "Modificar_Correcto",
    "Modificar_Datos",
    "Modificar_Boton",
    "Modificar_ruta"]),
    Cargar_Fechas(inicial, final) {
      const Meses = [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December",
      ];
      const Horario = [
        {
          dia: 1,
          hora_I: "13:30:00",
          hora_F: "15:00:00",
          materia: "Algoritmos Avanzados",
          Grupo: "3CV10",
          Tipo: 3,
        },
        {
          dia: 2,
          hora_I: "15:00:00",
          hora_F: "16:30:00",
          materia: "Algoritmos Geneticos",
          Grupo: "3CV10",
          Tipo: 3,
        },
        {
          dia: 6,
          hora_I: "13:30:00",
          hora_F: "15:00:00",
          materia: "Algoritmos Pequeños",
          Grupo: "3CV10",
          Tipo: 3,
        },
      ];
      let Valores = [];
      const events = [];
      Horario.forEach((element) => {
        Valores.push(element.dia);
      });
      const fecha = new Date();
      fecha.setMonth(inicial.getMonth());
      fecha.setFullYear(inicial.getFullYear());
      
      for (let dia = inicial.getDate(); dia <= final.getDate(); dia++) {
        fecha.setDate(dia);
        if (Valores.includes(fecha.getDay())) {
        
          let busco = fecha.getDay();
          const datos = Horario.find((elemento) => elemento.dia == busco);
        
          const inicio = new Date(
            Meses[fecha.getMonth()] +
              " " +
              fecha.getDate() +
              ", " +
              fecha.getFullYear() +
              " " +
              datos.hora_I
          );
          const final = new Date(
            Meses[fecha.getMonth()] +
              " " +
              fecha.getDate() +
              ", " +
              fecha.getFullYear() +
              " " +
              datos.hora_F
          );
          events.push({
            name: datos.materia,
            start: inicio,
            end: final,
            color: "orange",
            timed: true,
          });
        }
      }
      
      this.events = events;
      //let dia = new Date();
    },
    viewDay({ date }) {
      this.focus = date;
      this.type = "day";
    },
    getEventColor(event) {
      return event.color;
    },
    setToday() {
      this.focus = "";
    },
    prev() {
      this.$refs.calendar.prev();
    },
    next() {
      this.$refs.calendar.next();
    },
    showEvent({ nativeEvent, event }) {
      const open = () => {
        this.selectedEvent = event;
        this.selectedElement = nativeEvent.target;
        
        requestAnimationFrame(() =>
          requestAnimationFrame(() => (this.selectedOpen = true))
        );
      };

      if (this.selectedOpen) {
        this.selectedOpen = false;
        requestAnimationFrame(() => requestAnimationFrame(() => open()));
      } else {
        open();
      }

      nativeEvent.stopPropagation();
    },

    //updateRange({ start, end }) {
      updateRange(){
     // const min = new Date(`${start.date}T00:00:00`);
      //const max = new Date(`${end.date}T23:59:59`);
      this.events=[];
      //console.log(start,end);
      //?this.Faltas_Docentes({ start, end })
      //?this.Asistencia_Docente({ start, end })
      //this.Cargar_Fechas(min, max);
      /*console.log("--------------------------");
      console.log(end.date);
      console.log(start.date);
      console.log("--------------------------");
      console.log(min);
      console.log(max);
      const days = (max.getTime() - min.getTime()) / 86400000;
      const eventCount = this.rnd(days, days + 20);
      for (let i = 0; i < eventCount; i++) {
        const allDay = this.rnd(0, 3) === 0;
        const firstTimestamp = this.rnd(min.getTime(), max.getTime());
        const first = new Date(firstTimestamp - (firstTimestamp % 900000));
        const secondTimestamp = this.rnd(2, allDay ? 288 : 8) * 900000;
        const second = new Date(first.getTime() + secondTimestamp);
        events.push({
          name: this.names[this.rnd(0, this.names.length - 1)],
          start: first,
          end: second,
          color: this.colors[this.rnd(0, this.colors.length - 1)],
          timed: !allDay,
          details: "Hoal",
        });
      }*/
      
    },
    rnd(a, b) {
      return Math.floor((b - a + 1) * Math.random()) + a;
    },
    async Asistencia_Docente({ start, end }){
      //this.Modificar_cargar(true);      
      this.information='';
      this.Espera=true;
      this.error = false;            
      const json = JSON.stringify({
        id:this.No_Trabajador,        
        fechaI:start.date,
        fechaF:end.date,
      });      
        let res = await axios.post("http://"+this.ip+"/api/docente/Registro-Rango", json, {
          headers: {
            // Overwrite Axios's automatically set Content-Type
            "Content-Type": "application/json",
          },
        }).catch(error =>{
          this.Espera = false;
          this.Correcto=false;  
          this.error=true;
          if(error.response.status == 400){
            this.information=error.response.data.error;            
          }
        })
        if(!this.error && !res.data.Error){          
          this.Espera = false;
          this.Correcto=true;
          this.information="Registro Cargados";  
          const Faltas = []
          
          res.data.Registros.forEach(element => {
            const aux = new Date(parseInt(element.Fecha.substring(0,4),10),parseInt(element.Fecha.substring(5,7),10)-1,parseInt(element.Fecha.substring(8,10),10))            
            const dato ={
              name: "Reunion",
              start: aux,
              end: aux,
              color: "green",
              timed: false,
            }            
            if(!Faltas.includes(dato)){
              Faltas.push(dato)
            }           
          });
     
          Faltas.forEach(element => {
              if(!this.events.includes(element)){
                this.events.push(element);
              }
          });
         
        }
        else if(res.data.Error){          
          this.Espera = false;
          this.Correcto=true;  
          this.error=false;
          this.information="No hay faltas para mostrar";
        }
    },
    async Faltas_Docentes({ start, end }){
      //this.Modificar_cargar(true);      
      this.information='';
      this.Espera=true;
      this.error = false;            
      const json = JSON.stringify({
        id:this.No_Trabajador,        
        fechaI:start.date,
        fechaF:end.date,
      });      
        let res = await axios.post("http://"+this.ip+"/api/docente/Falta-Rango", json, {
          headers: {
            // Overwrite Axios's automatically set Content-Type
            "Content-Type": "application/json",
          },
        }).catch(error =>{
          this.Espera = false;
          this.Correcto=false;  
          this.error=true;
          if(error.response.status == 400){
            this.information=error.response.data.error;            
          }
        })
        if(!this.error && !res.data.Error){          
          this.Espera = false;
          this.Correcto= true;
          this.information="Faltas Cargadas";  
          const Faltas = []
        
        
          res.data.Faltas.forEach(element => {
            const aux = new Date(parseInt(element.Fecha.substring(0,4),10),parseInt(element.Fecha.substring(5,7),10)-1,parseInt(element.Fecha.substring(8,10),10))            
            const dato ={
              name: "Falta",
              start: aux,
              end: aux,
              color: "red",
              timed: false,
            }            
            if(!Faltas.includes(dato)){
              Faltas.push(dato)
            }           
          });
          Faltas.forEach(element => {
              if(!this.events.includes(element)){
                this.events.push(element);
              }
          });          
        }
        else if(res.data.Error){          
          this.Espera = false;
          this.Correcto=true;  
          this.error=false;
          this.information="No hay faltas para mostrar";
        }
    }
  },
  
  computed:{
     ...mapState(['ip','No_Trabajador']),
  }
};
</script>
